import React, { Component } from "react";

class Counters extends Component {
    render() {
        return (
            <div>
                <h4 class="mt-3">Your 2019 Stats</h4>
                <div class="row">
                    
                        <div class="col cat_blue m-2">
                            <small>Run:</small><br />
                            <span class="display-4">51</span> km
                        </div>
                    
                    
                        <div class="col cat_green m-2">
                            <small>Strength:</small><br />
                            <span class="display-4">52</span> sets
                        </div>
                    
                        <div class="col cat_green_outline m-2">
                            <small>Stretch:</small><br />
                            <span class="display-4">9</span> sets
                        </div>
                    
                        <div class="col cat_red m-2">
                            <small>Balance:</small><br />
                            <span class="display-4">35</span> sets
                        </div>
                    
                </div>
            </div>
        );
    }
}

export default Counters;

